* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
  box-sizing: border-box;
}
body {
  background-image: url("../src/images/pexels-photo-235985pixabay.jpeg");
  background-attachment: fixed;
}
.navbar a, .navbar a:visited, .navbar a:active {
  display: block;
  width: 100%;
  height: 100%;
  color: #bbd;
  border: 1px solid blue;
  display: flex;
  justify-content: center;
  align-items: center;
}
.darken5 {
  background-color: rgba(0,0,5,0.5);
}
._100W {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
ul.navbar {
  display: flex;
  justify-content: space-evenly;
  min-width: 600px;
  max-width: 1200px;
  margin: auto;
  width: 90%;
  height: 50px;
  background-color: black;
}
ul.navbar li {
  display: flex;
  justify-content: center;
  align-items: center;
}
ul.navbar > li {
  flex: 20%;
  color: #88c;
  min-width: 125px;
  height: 100%;
  position: relative;
  border: 1px solid blue;
}
.navbar > li:hover .dropdown {
  display: block;
}
.dropdown {
  display: none;
  position: absolute;
  width: 100%;
  top: 100%;
}

.dropdown > li {
  height: 40px;
  border: 1px solid #187;
}

